'use client';

import { useRef } from 'react';
import { Provider } from 'react-redux';
import { configureStore } from '@reduxjs/toolkit';
import authReducer from '@/store/slices/authSlice';
import profileReducer from '@/store/slices/profileSlice';
import resumeReducer from '@/store/slices/resumeSlice';
import templatesReducer from '@/store/slices/templatesSlice';
import atsReducer from '@/store/slices/atsSlice';
import uiReducer from '@/store/slices/uiSlice';

export default function ReduxProvider({
  children,
}: {
  children: React.ReactNode;
}) {
  const storeRef = useRef<ReturnType<typeof configureStore>>();
  
  if (!storeRef.current) {
    storeRef.current = configureStore({
      reducer: {
        auth: authReducer,
        profile: profileReducer,
        resume: resumeReducer,
        templates: templatesReducer,
        ats: atsReducer,
        ui: uiReducer,
      },
    });
  }

  return <Provider store={storeRef.current}>{children}</Provider>;
}
